.dreams-intro {
    position: relative;
    height: 100vh;

    &__overlay {
        position: absolute;
        background-image: url('../img/drims/intro/intro.png');
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -2;
    }

    &__shape {
        position: absolute;
        background: rgb(0,0,0);
        background: linear-gradient(180deg, rgba(0,0,0,1) 50%, rgba(0,0,0,0.8960376386882878) 57%, rgba(0,0,0,0.8) 64%, rgba(0,0,0,0.7) 70%, rgba(0,0,0,0.6) 74%, rgba(255,255,255,0) 100%);
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
    }

    .container {
        height: 100%;
    }

    &__content {
        height: 100%;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &__title {
        width: 815px;
        height: auto;
        object-fit: cover;

        margin-bottom: 1.8rem;
    }

    &__text {
        font-family: 'Gilroy', sans-serif;
        font-weight: 500;
        font-size: 4.2rem;
        line-height: 1.1;

        margin-bottom: 3rem
    }

    &__text-bottom {
        font-family: 'Gilroy', sans-serif;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 1.1;
    }

    &__button {
        border: .2rem solid #fff;
        border-radius: 2rem;
        width: fit-content;
        height: 3.4rem;

        font-family: 'Gilroy', sans-serif;
        font-weight: 500;
        font-size: 2rem;
        line-height: 1.1;
        text-transform: uppercase;
        color: #fff;

        text-align: center;

        padding-inline: 2rem;

        position: absolute;
        bottom: 5rem;

        transition: .3s ease filter;
        
        @media (hover: hover) {
            &:hover {
                -webkit-filter: drop-shadow(0 0 7.92px #fff) drop-shadow(0 0 3.96px #fff);
                filter: drop-shadow(0 0 7.92px #fff) drop-shadow(0 0 3.96px #fff);
            }
        }
    }
}
